<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  props: ["mOptions", "mChartData"],
  data: () => ({
  }),
  methods: {},
  created() {
  },
  watch: {
    mChartData() {
      if (this.$data._chart == null) {
        this.renderChart(this.mChartData, this.mOptions);
      }

      this.$data._chart.config.data.labels = this.mChartData.labels;
      this.$data._chart.config.data.datasets.forEach((item, index) => {
        item.data = this.mChartData.datasets[index].data;
      });
      this.$data._chart.update();
    }
  }
};
</script>