          <tr>
            <th class="text-left">DB Name</th>
            <th class="text-left">Connections</th>
            <th class="text-left">Transactions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in items" :key="item.name">
            <td>{{ item.name }}</td>
            <td>{{ item.connections }}</td>
            <td>{{ item.transaction }}</td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </v-container>
