<template>
  <v-container class="pa-0 ma-0">
    <line-chart style="height: 200px" :mOptions="mOptions" :mChartData="mChartData"></line-chart>
    <v-simple-table dense>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left">Module</th>
            <th class="text-left">Count</th>
            <th class="text-left">Traffic</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in items" :key="item.name">
            <td>{{ item.name }}</td>
            <td>{{ item.count }}</td>
            <td>{{ item.traffic }}</td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </v-container>
</template>
<script>

import { utils } from "@/mixins/utils";
export default {
  mixins: [utils],
  data() {
    return {
      items: [
        {
          name: "LTE",
          count: 15,
          traffic: 312432
        },
        {
          name: "NB-IoT",
          count: 3,
          traffic: 13222
        },
        {
          name: "LoRa",
          count: 7,
          traffic: 3124
        }
      ],
      mOptions: {
        responsive: true,
        maintainAspectRatio: false,
        title: {
          display: true,
          text: "network traffic chart"
        },
        legend: {
          position: "bottom"
        }
      },
      mChartData : null
    }
  },
  created(){
      setInterval(()=>{
          this.mChartData={
              labels: [
                this.getRandomInt(),
                this.getRandomInt(),
                this.getRandomInt(),
                this.getRandomInt()
              ],
              datasets: [
                {
                  label: "LTE",
                  backgroundColor: "#ffffff",
                  data: [
                    this.getRandomInt(),
                    this.getRandomInt(),
                    this.getRandomInt(),
                    this.getRandomInt()
                  ]
                },
                {
                  label: "NB-IoT",
                  backgroundColor: "#ffffff",
                  data: [
                    this.getRandomInt(),
                    this.getRandomInt(),
                    this.getRandomInt(),
                    this.getRandomInt()
                  ]
                },
                {
                  label: "RoLa",
                  backgroundColor: "#ffffff",
                  data: [
                    this.getRandomInt(),
                    this.getRandomInt(),
                    this.getRandomInt(),
                    this.getRandomInt()
                  ]
                }
              ]
            } 
      },1000);
  }
};
</script>