<template>
  <v-card class="fill-height">
    <p class="light-blue white--text pa-2 ma-0 pl-5" style="
    background: #3A1C71;
    background: -webkit-linear-gradient(to right,#3F51B5, #2196F3);
    background: linear-gradient(to right, #3F51B5 , #2196F3);
  ">장치정보</p>
    <v-container class="mt-2">
      <v-text-field
        label="장치아이디"
        :value="device==null?'':device.bike_id"
        placeholder="device_id"
        outlined
        readonly
        rounded
        dense
      ></v-text-field>
      <v-text-field
        label="장치타입"
        :value="device==null?'':device.type"
        placeholder="device_type"
        outlined
        readonly
        rounded
        dense
      ></v-text-field>
      <v-text-field
        label="현재위치"
        :value="device==null?'':device.address"
        placeholder="location"
        outlined
        readonly
        rounded
        dense
      ></v-text-field>
      <v-text-field
        label="상태"
        :value="device==null?'':getStatus"
        placeholder="status"
        outlined
        readonly
        rounded
        dense
      ></v-text-field>
      <v-text-field
        label="배터리"
        :value="device==null?'':device.battery+'%'"
        placeholder="battery"
        outlined
        readonly
        rounded
        dense
      ></v-text-field>
      <v-text-field
        label="업데이트 시간"
        :value="device==null?'':device.timestamp"
        placeholder="update_time"
        outlined
        readonly
        rounded
        dense
      ></v-text-field>
    </v-container>
    <p class="caption mb-0 text-center">지도에서 장치를 선택해주세요.</p>
  </v-card>
</template>
<script>
import { EventBus } from "@/main";
export default {
  props: ["device"],
  mounted() {
    EventBus.$on("d", device => {
      this.device = device;
    });
  },
  computed: {
    getStatus() {
      if (this.device != null) {
        if (this.device.status == 1) {
          return "정상";
        } else if (this.device.status == 2) {
          return "수신불량";
        } else {
          return "배터리부족";
        }
      }
    }
  }
};
</script>