<template>
  <v-row class="mt-3" no-gutters>
    <v-col sm="4">
      <v-card>
        <p
          class="green white--text pa-2 ma-0 pl-5"
          style="
    background: #3A1C71;
    background: -webkit-linear-gradient(to right, #D76D77,#3A1C71 , #2196F3);
    background: linear-gradient(to right, #D76D77 ,#3A1C71 , #2196F3);
  "
        >전체 디바이스</p>
        <v-row class="mx-1">
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">전체</p>
            <v-dialog scrollable :width="600">
              <template v-slot:activator="{ on }">
                <v-btn text large outlined v-on="on" class="title">{{allDeviceInfo.total}}</v-btn>
              </template>
              <v-card>
                <v-card-title>Device Info</v-card-title>
                <v-divider></v-divider>
                <v-card-text style="height: 1000px;">
                  <v-list>
                    <v-list-item v-for="(device,index) in allDevice" :key="index">
                      <v-list-item-content>
                        <v-list-item-title>Device_id : {{ device.bike_id }}</v-list-item-title>
                        <v-list-item-title>Address : {{ device.address }}</v-list-item-title>
                        <v-list-item-title>Battery : {{ device.battery }}%</v-list-item-title>
                        <v-list-item-title>Update Time : {{ device.timestamp }}</v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-dialog>
          </v-col>
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">정상</p>
            <v-btn text large outlined class="title light-green--text">{{allDeviceInfo.normal}}</v-btn>
          </v-col>
        </v-row>
        <!-- <p class="light-green white--text pa-2 ma-0 text-center">점검</p> -->
        <v-row class="mx-1">
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">수신상태불량</p>
            <v-btn
              text
              large
              outlined
              class="title"
              :class="{'red--text':allDeviceInfo.broken>0}"
            >{{allDeviceInfo.broken}}</v-btn>
          </v-col>
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">배터리부족</p>
            <v-btn
              text
              large
              outlined
              class="title"
              :class="{'yellow--text':allDeviceInfo.battery>0}"
            >{{allDeviceInfo.battery}}</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
    <v-col sm="4">
      <v-card class="mx-3">
        <p
          class="blue white--text pa-2 ma-0 pl-5"
          style="
    background: #3A1C71;
    background: -webkit-linear-gradient(to right,#3F51B5, #2196F3);
    background: linear-gradient(to right, #3F51B5 , #2196F3);
  "
        >이동형 디바이스</p>
        <v-row class="mx-1">
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">이동형 전체</p>
            <v-btn text large outlined class="title">{{movementDeviceInfo.total}}</v-btn>
          </v-col>
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">정상</p>
            <v-btn text large outlined class="title light-green--text">{{movementDeviceInfo.normal}}</v-btn>
          </v-col>
        </v-row>
        <!-- <p class="light-blue  white--text pa-2 ma-0 text-center">점검</p> -->
        <v-row class="mx-1">
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">수신상태불량</p>
            <v-btn
              text
              large
              outlined
              class="title"
              :class="{'red--text':movementDeviceInfo.broken>0}"
            >{{movementDeviceInfo.broken}}</v-btn>
          </v-col>
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">배터리부족</p>
            <v-btn
              text
              large
              outlined
              class="title"
              :class="{'yellow--text':movementDeviceInfo.battery>0}"
            >{{movementDeviceInfo.battery}}</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
    <v-col sm="4">
      <v-card>
        <p
          class="light-blue white--text white--text pa-2 ma-0 pl-5"
          style="
    background: #3A1C71;
    background: -webkit-linear-gradient(to right,#3F51B5, #2196F3);
    background: linear-gradient(to right, #3F51B5 , #2196F3);
  "
        >고정형 디바이스</p>
        <v-row class="mx-1">
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">고정형 전체</p>
            <v-btn text large outlined class="title">{{fixedDeviceInfo.total}}</v-btn>
          </v-col>
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">정상</p>
            <v-btn text large outlined class="title light-green--text">{{fixedDeviceInfo.normal}}</v-btn>
          </v-col>
        </v-row>
        <!-- <p class="light-blue  white--text pa-2 ma-0 text-center">점검</p> -->
        <v-row class="mx-1">
          <v-col sm="6" style="text-align:center">
            <p class="subtitle-1 mb-2">수신상태불량</p>
            <v-btn
              text
              large
              outlined
              class="title"
              :class="{'red--text':fixedDeviceInfo.broken>0}"
            >{{fixedDeviceInfo.broken}}</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-row>
</template>
<script>
import { mapActions, mapMutations, mapState, mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["allDevice"]),
    allDeviceInfo() {
      let total = 0;
      let normal = 0;
      let broken = 0;
      let battery = 0;
      this.allDevice.forEach(item => {
        if (item.status == 1) normal++;
        if (item.status == 2) broken++;
        if (item.status == 3) battery++;
        total++;
      });

      return {
        total: total,
        normal: normal,
        broken: broken,
        battery: battery
      };
    },
    movementDeviceInfo() {
      let total = 0;
      let normal = 0;
      let broken = 0;
      let battery = 0;
      this.allDevice.forEach(item => {
        if (item.type == 1) {
          if (item.status == 1) normal++;
          if (item.status == 2) broken++;
          if (item.status == 3) battery++;
          total++;
        }
      });

      return {
        total: total,
        normal: normal,
        broken: broken,
        battery: battery
      };
    },
    fixedDeviceInfo() {
      let total = 0;
      let normal = 0;
      let broken = 0;
      this.allDevice.forEach(item => {
        if (item.type == 2) {
          if (item.status == 1) normal++;
          if (item.status == 2) broken++;
          total++;
        }
      });

      return {
        total: total,
        normal: normal,
        broken: broken
      };
    }
  },
  methods: {},
  watch: {
    allDevice() {}
  }
};
</script>